<template>
  <div class="skills-list" v-smooth-appear="{elements: '.smooth-appear-skill', duration: 500, delay: 10, translate: 30}">
    <template v-for="(item, index) in items">
      <br v-if="item === '<br>'" :key="index" />
      <div v-else class="item smooth-appear-skill" :key="index">
        {{ item }}
      </div>
    </template>
  </div>
</template>

<script>
import SmoothAppear from '@/directives/SmoothAppear'

export default {
  props: ['items'],
  directives: {
    SmoothAppear
  }
}
</script>

<style scoped>

.skills-list {
}
.item {
  display: inline-block;
  padding: 5px 10px;
  cursor: default;
  /* transition: all 0.5s ease; */
}
.item:hover {
  /* transform: scale(1.2); */
}
</style>



